* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-height: 200px;
}

.card {
  align-items: center;
  border: 2px solid grey;
  border-radius: 7px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 300px;
  max-height: 360px;
  max-width: 205px;
}

.card p {
  padding: 5px;
  text-align: center;
  clear: right;
}

.card img {
  max-height: 300px;
  max-width: 220px;
}

/* Anime search card */

.cardSearch {
  align-items: center;
  border: 2px solid grey;
  border-radius: 7px;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: auto;
  margin-bottom: 10px;
  margin-right: 10px;
  max-height: 600px;
  max-width: 400px;
  overflow: auto;
  padding: 10px;
  width: 255px;
}
.cardSearch img {
  border: 0.2px solid gray;
  height: auto;
  max-height: 400px;
  max-width: 235px;
  width: auto;
  
}
.cardSeacrh p {
  text-align: center;
}